export class GameConfig {

    public static DEFAULT_LIVES_COUNT = 3;

    public static DEFAULT_SCREEN_SIZE_AREA = 800 * 600;
    public static DEFAULT_ASTEROIDS_MIN_COUNT = 2;
    public static DEFAULT_ASTEROID_MAX_SIZE = 30;

    public static DEFAULT_CAMERA_SHAKE_MAGNITUDE_FOR_ASTEROID = GameConfig.DEFAULT_ASTEROID_MAX_SIZE / 8;
    public static DEFAULT_CAMERA_SHAKE_TIME_FOR_ASTEROID = 2;

    public static DEFAULT_CAMERA_SHAKE_MAGNITUDE_FOR_SPACESHIP = GameConfig.DEFAULT_ASTEROID_MAX_SIZE / 4;
    public static DEFAULT_CAMERA_SHAKE_TIME_FOR_SPACESHIP = 3;
    
    public static SCALE_SCREEN_TO_DEFAULT_SCREEN_SIZE_AREA = true;

    public width: number;
    public height: number;

    public constructor(width: number, height: number) {
        this.width = width;
        this.height = height;
    }
}
